          </p>

          <p style="text-align: justify !important">
            5. Anda dapat memberitahukan permintaan Anda kepada Kami, untuk
            menghapus Data Anda (baik data-data tertentu atau keseluruhan data
            Anda), dalam hal itu, Anda dapat mengajukan permintaan penghapusan
            data dengan cara email ke
            <a style="font-color: blue; text-decoration: underline"
              >alfacare@sat.co.id</a
            >. Kami dapat mengabulkan permintaan tersebut sejauh diizinkan
            berdasarkan ketentuan perundang-undangan yang berlaku.
