        </v-card-text>

        <v-card-actions class="pa-4">
          <v-spacer></v-spacer>
          <!--   <v-btn color="green darken-1" text @click="dialog = false">
            Disagree
          </v-btn> -->
          <v-btn
            color="green darken-1"
            text
            @click="dialogTaC = false"
            style="font-weight: bold !important"
          >
            Setuju
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-form
      class="ma-0 pa-0 px-3"
      style="max-width: 500px !important; height: 150vh !important"
    >
      <h2 class="my-4">Dapatkan {{ product_name }}!</h2>
      <v-container width="100%" fluid>
        <v-row width="100%" class="d-flex justify-center">
          <v-col cols="12" sm="12" width="100%" class="ma-0 pa-0">
            <v-text-field
              label="Nama Lengkap"
              v-model="input.name"
              :rules="textInputRules"
              outlined
              required
              width="100%"
            ></v-text-field>
          </v-col>

          <v-col cols="12" sm="12" width="100%" class="ma-0 pa-0">
            <v-autocomplete
              label="Provinsi"
              v-model="input.provinsi"
              :rules="textInputRules"
              :items="provinsi_data"
              item-text="tbmprov_nama"
              item-value="tbmprov_provinsi_id"
              required
              outlined
              auto-select-first
              clearable
              @change="triggerFilterKota()"
            ></v-autocomplete>
          </v-col>
          <v-col cols="12" sm="12" width="100%" class="ma-0 pa-0">
            <v-autocomplete
              label="Kota/Kabupaten"
              v-model="input.kabupaten"
              :rules="textInputRules"
              :items="filtered_kabupaten_data"
              item-text="tbmkt_nama"
              item-value="tbmkt_kota_id"
              required
              outlined
              auto-select-first
              clearable
              @change="triggerFilterKecamatan()"
            ></v-autocomplete>
          </v-col>
          <v-col cols="12" sm="12" width="100%" class="ma-0 pa-0">
            <v-autocomplete
              label="Kecamatan"
              v-model="input.kecamatan"
              :rules="textInputRules"
              :items="filtered_kecamatan_data"
              item-text="tbmkct_nama"
              item-value="tbmkct_kecamatan_id"
              required
              outlined
              auto-select-first
              clearable
            ></v-autocomplete>
          </v-col>
          <v-col cols="12" sm="12" width="100%" class="ma-0 pa-0">
            <v-text-field
              label="Alamat Pengiriman"
              v-model="input.alamat"
              :rules="textInputRules"
              outlined
              required
              width="100%"
              hint="**pastikan alamat tujuan Anda benar karena barang akan dikirimkan sesuai dengan alamat yang terdaftar."
              persistent-hint
            ></v-text-field>
          </v-col>

          <v-col cols="12" sm="12" width="100%" class="ma-0 pa-0">
            <v-text-field
              label="Email"
              v-model="input.email"
              :rules="emailRules"
              outlined
              required
              width="100%"
              hint="**pastikan alamat email Anda benar karena verifikasi akan dikirimkan lewat email."
              persistent-hint
            ></v-text-field>
          </v-col>
          <v-col cols="12" sm="12" width="100%" class="ma-0 pa-0 mt-3">
            <v-text-field
              label="Handphone"
              v-model="input.phone"
              :rules="phoneNumberRules"
              outlined
              required
              width="100%"
            ></v-text-field>
          </v-col>
        </v-row>

        <v-row width="100%">
          <v-col
            width="100%"
            class="d-flex justify-start align-center pa-0 mt-16 mb-8"
          >
            <v-checkbox
              class="ma-0 pa-0"
              v-model="checkbox"
              color="success"
              hide-details
            ></v-checkbox>
            <!-- <div style="font-size: 12px !important; letter-spacing: 0.6px">
              Saya setuju untuk menerima komunikasi via
              email/whatsapp/sms/telepon dari
              <a target="_blank" href="https://gli.id/">
                PT. Global Loyalty Indonesia
              </a>
              (Alfa Group) mengenai program ini dan program lain yang relevan.
            </div> -->

            <div
              class="ma-0 pa-0 mt-auto"
              style="font-size: 12px !important; letter-spacing: 0.6px"
            >
              Saya telah membaca dan setuju syarat dan ketentuan terkait
              <a
                @click="dialogTaC = true"
                style="font-color: blue; text-decoration: underline"
              >
                Kebijakan Data Pribadi/Kebijakan Privasi</a
              >
              ini
            </div>
          </v-col>
        </v-row>

        <v-row width="100%" class="d-flex justify-start">
