<script>
/* eslint-disable */
import axios from "axios";
import { api_url } from "../global_variable/global_variable.js";
import sha256 from "js-sha256";

import { data_provinsi } from "../global_variable/data_provinsi.js";
import { data_kota } from "../global_variable/data_kota.js";
import { data_kecamatan } from "../global_variable/data_kecamatan.js";

export default {
  name: "Form",

  components: {},

  data() {
    return {
      flag: false,
      dialog: false,
      dialogTaC: false,
      pe_id: null,
      voucher_type: null,
      pe_end_date: null,
      subject_mail: null,
      checkbox: false,

      input: {
        name: null,
        email: null,
        phone: "08",
        provinsi: "",
        kabupaten: "",
        kecamatan: "",
        alamat: "",
      },
      client_ip: 0,
      product_name: localStorage.product_name,
      provinsi_data: [],
      kabupaten_data: [],
      kecamatan_data: [],
      filtered_kabupaten_data: [],
      filtered_kecamatan_data: [],
      whitelist_kabupaten: localStorage.pe_whitelist_kabupaten,

      textInputRules: [(v) => !!v || "Kolom harus diisi"],
      emailRules: [
        (v) =>
          (!!v && /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v)) ||
          "E-mail tidak valid",
      ],
      phoneNumberRules: [
        (v) =>
          (v.length >= 10 &&
            String(v).substring(0, 1) == "0" &&
            /^\d+$/.test(v)) ||
          "Nomor Hp tidak valid",
      ],
      rules: {},
    };
  },

  created() {},

  async mounted() {
    if (!localStorage.voucher_token) {
      this.$router.push({ name: "notFound" });
    } else if (
      localStorage.client_ip == "" ||
      localStorage.client_ip == 0 ||
      localStorage.client_ip == null ||
      localStorage.client_ip == undefined
    ) {
      this.$router.push({ name: "ip-pernah-digunakan" });
    } else {
      localStorage.removeItem("date_end");
      localStorage.removeItem("voucher_token");
      this.pe_id = localStorage.pe_id;
      this.voucher_type = localStorage.voucher_type;
      this.pe_end_date = localStorage.pe_end_date;
      this.subject_mail = localStorage.subject_mail;
      this.client_ip = localStorage.client_ip;
      localStorage.removeItem("pe_id");
      localStorage.removeItem("voucher_type");
      localStorage.removeItem("pe_end_date");
      localStorage.removeItem("subject_mail");
      localStorage.removeItem("client_ip");
      localStorage.removeItem("product_name");
      this.whitelist_kabupaten = localStorage.pe_whitelist_kabupaten;
      localStorage.removeItem("pe_whitelist_kabupaten");
      try {
        var dateNow = new Date().toISOString().slice(0, 10).replace(/-/g, "");
        var tgl = dateNow.toString();
        var kunci = "SSO" + "4lfag1ft" + tgl + "precise";
        // console.log(kunci);
        var token = sha256(kunci);

        const headers = {
          "Access-Control-Allow-Origin": "*",
          "Access-Control-Allow-Credentials": "true",
          "Access-Control-Allow-Methods":
            "GET, PUT, POST, DELETE, OPTIONS, post, get",
          "Access-Control-Allow-Headers": "Origin, Content-Type, X-Auth-Token",
          "Content-Type": "application/json",
          "Content-Length": "<calculated when request is sent>",
          Host: "<calculated when request is sent>",
          Accept: "*/*",
          "Accept-Encoding": "gzip, deflate, br",
          Connection: "keep-alive",
          app: "sso",
        };

        await axios
          .get(
            api_url + "/check-voucher?token=" + token + "&pe_id=" + this.pe_id,
            headers
          )
          .then((response) => {
            // console.log(response.data);
            if (response.data.response_message != "success") {
              this.$router.push({ name: "item-habis" });
              localStorage.removeItem("voucher_token");
              localStorage.removeItem("pe_id");
              localStorage.removeItem("voucher_type");
              localStorage.removeItem("pe_end_date");
              localStorage.removeItem("subject_mail");
              localStorage.removeItem("client_ip");
              localStorage.removeItem("product_name");
              localStorage.removeItem("pe_whitelist_kabupaten");
            } else {
              this.getAddress();
              this.checkIp();
            }
          })
          .catch((error) => {
            console.log("Form.vue (error) :: ", error);
          });
      } catch (e) {
        console.error(e);
        alert("Error: koneksi gagal");
      }
    }

    // this.getAddress();
    // this.flag = true; //FIXME SET TO FALSE
  },

  methods: {
    async getAddress() {
      // DATA PROVINSI
      try {
        var dateNow = new Date().toISOString().slice(0, 10).replace(/-/g, "");
        var tgl = dateNow.toString();
        var kunci = "SSO" + "4lfag1ft" + tgl + "precise";
        // console.log(kunci);
        var token = sha256(kunci);

        const headers = {
          "Access-Control-Allow-Origin": "*",
          "Access-Control-Allow-Credentials": "true",
          "Access-Control-Allow-Methods":
            "GET, PUT, POST, DELETE, OPTIONS, post, get",
          "Access-Control-Allow-Headers": "Origin, Content-Type, X-Auth-Token",
          "Content-Type": "application/json",
          "Content-Length": "<calculated when request is sent>",
          Host: "<calculated when request is sent>",
          Accept: "*/*",
          "Accept-Encoding": "gzip, deflate, br",
          Connection: "keep-alive",
          app: "sso",
        };

        // console.log(this.whitelist_kabupaten);
        await axios
          .get(
            api_url +
              "/get-address?token=" +
              token +
              "&type=" +
              "provinsi" +
              "&whitelist_kabupaten=" +
              this.whitelist_kabupaten,
            headers
          )
          .then((response) => {
            // console.log(response.data);
            if (response.data.response_message == "success") {
              this.provinsi_data = response.data.data;
              if (response.data.data.length == 0) {
                this.provinsi_data = data_provinsi;
              }
            } else {
              console.error("Error: failed to get provinsi");
              this.provinsi_data = data_provinsi;
            }
          })
          .catch((error) => {
            console.log("Form.vue (error) :: ", error);
          });
      } catch (e) {
        console.error(e);
        alert("Error: koneksi gagal");
      }

      // DATA KOTA
      try {
        var dateNow = new Date().toISOString().slice(0, 10).replace(/-/g, "");
        var tgl = dateNow.toString();
        var kunci = "SSO" + "4lfag1ft" + tgl + "precise";
        // console.log(kunci);
        var token = sha256(kunci);

        const headers = {
          "Access-Control-Allow-Origin": "*",
          "Access-Control-Allow-Credentials": "true",
          "Access-Control-Allow-Methods":
            "GET, PUT, POST, DELETE, OPTIONS, post, get",
          "Access-Control-Allow-Headers": "Origin, Content-Type, X-Auth-Token",
          "Content-Type": "application/json",
          "Content-Length": "<calculated when request is sent>",
          Host: "<calculated when request is sent>",
          Accept: "*/*",
          "Accept-Encoding": "gzip, deflate, br",
          Connection: "keep-alive",
          app: "sso",
        };

        await axios
          .get(
            api_url +
              "/get-address?token=" +
              token +
              "&type=" +
              "kota" +
              "&whitelist_kabupaten=" +
              this.whitelist_kabupaten,
            headers
          )
          .then((response) => {
            // console.log(response.data);
            if (response.data.response_message == "success") {
              this.kabupaten_data = response.data.data;
              if (response.data.data.length == 0) {
                this.kabupaten_data = data_kota;
              }
            } else {
              console.error("Error: failed to get kota");
              this.kabupaten_data = data_kota;
            }
          })
          .catch((error) => {
            console.log("Form.vue (error) :: ", error);
          });
      } catch (e) {
        console.error(e);
        alert("Error: koneksi gagal");
      }
      this.filtered_kabupaten_data = this.kabupaten_data;

      // LOOPING tbmkt_kota_id FOR FILTER DATA KECAMATAN
      var temp_tbmkt_kota_id_arr = [];
      for (let i = 0; i < this.kabupaten_data.length; i++) {
        temp_tbmkt_kota_id_arr.push(this.kabupaten_data[i]["tbmkt_kota_id"]);
      }
      // console.log(temp_tbmkt_kota_id_arr.toString().replace(" ", ""));

      // DATA KECAMATAN
      try {
        var dateNow = new Date().toISOString().slice(0, 10).replace(/-/g, "");
        var tgl = dateNow.toString();
        var kunci = "SSO" + "4lfag1ft" + tgl + "precise";
        // console.log(kunci);
        var token = sha256(kunci);

        const headers = {
          "Access-Control-Allow-Origin": "*",
          "Access-Control-Allow-Credentials": "true",
          "Access-Control-Allow-Methods":
            "GET, PUT, POST, DELETE, OPTIONS, post, get",
          "Access-Control-Allow-Headers": "Origin, Content-Type, X-Auth-Token",
          "Content-Type": "application/json",
          "Content-Length": "<calculated when request is sent>",
          Host: "<calculated when request is sent>",
          Accept: "*/*",
          "Accept-Encoding": "gzip, deflate, br",
          Connection: "keep-alive",
          app: "sso",
        };

        await axios
          .get(
            api_url +
              "/get-address?token=" +
              token +
              "&type=" +
              "kecamatan" +
              "&whitelist_kota=" +
              temp_tbmkt_kota_id_arr.toString().replace(" ", ""),
            headers
          )
          .then((response) => {
            // console.log(response.data);
            if (response.data.response_message == "success") {
              this.kecamatan_data = response.data.data;
              if (response.data.data.length == 0) {
                this.kecamatan_data = data_kecamatan;
              }
            } else {
              console.error("Error: failed to get kecamatan");
              this.kecamatan_data = data_kecamatan;
            }
          })
          .catch((error) => {
            console.log("Form.vue (error) :: ", error);
          });
      } catch (e) {
        console.error(e);
        alert("Error: koneksi gagal");
      }
      this.filtered_kecamatan_data = this.kecamatan_data;
    },
    async checkIp() {
      // console.log(this.client_ip);
      try {
        var dateNow = new Date().toISOString().slice(0, 10).replace(/-/g, "");
        var tgl = dateNow.toString();
        var kunci = "SSO" + "4lfag1ft" + tgl + "precise";
        // console.log(kunci);
        var token = sha256(kunci);

        const headers = {
          "Access-Control-Allow-Origin": "*",
          "Access-Control-Allow-Credentials": "true",
          "Access-Control-Allow-Methods":
            "GET, PUT, POST, DELETE, OPTIONS, post, get",
          "Access-Control-Allow-Headers": "Origin, Content-Type, X-Auth-Token",
          "Content-Type": "application/json",
          "Content-Length": "<calculated when request is sent>",
          Host: "<calculated when request is sent>",
          Accept: "*/*",
          "Accept-Encoding": "gzip, deflate, br",
          Connection: "keep-alive",
          app: "sso",
        };

        await axios
          .get(
            api_url +
              "/check-ip?token=" +
              token +
              "&pe_id=" +
              this.pe_id +
              "&client_ip=" +
              this.client_ip,
            headers
          )
          .then((response) => {
            // console.log(response.data);
            if (response.data.response_message != "success") {
              this.$router.push({ name: "ip-pernah-digunakan" });
            } else if (response.data.data.ip_status == true) {
              this.$router.push({ name: "ip-pernah-digunakan" });
            } else {
              // Pass all validation and check
              this.flag = true;
            }
          })
          .catch((error) => {
            console.log("Form.vue (error) :: ", error);
          });
      } catch (e) {
        console.error(e);
        alert("Error: koneksi gagal");
      }
    },

    triggerFilterKota() {
      if (
        this.input.provinsi != "" &&
        this.input.provinsi != null &&
        this.input.provinsi != undefined
      ) {
        // console.log(this.input.provinsi);

        let selected_provinsi = this.provinsi_data.find(
          (o) => o.tbmprov_provinsi_id == this.input.provinsi
        );
        // console.log(selected_provinsi);

        var tempObj = { obj: this.kabupaten_data };

        var newArray = tempObj.obj.filter(function (el) {
          return el.tbmprov_bps_id == selected_provinsi.tbmprov_bps_id;
        });
        this.filtered_kabupaten_data = newArray;
        // console.log(selected_provinsi.tbmprov_bps_id, newArray);
      } else {
        this.filtered_kabupaten_data = this.kabupaten_data;
      }
    },
    triggerFilterKecamatan() {
      if (
        this.input.kabupaten != "" &&
        this.input.kabupaten != null &&
        this.input.kabupaten != undefined
      ) {
        // console.log(this.input.kabupaten);

        let selected_kabupaten = this.kabupaten_data.find(
          (o) => o.tbmkt_kota_id == this.input.kabupaten
        );
        // console.log(selected_kabupaten);

        var tempObj = { obj: this.kecamatan_data };

        var newArray = tempObj.obj.filter(function (el) {
          return el.tbmkt_kota_id == selected_kabupaten.tbmkt_kota_id;
        });
        this.filtered_kecamatan_data = newArray;
        // console.log(selected_provinsi.tbmprov_bps_id, newArray);
      } else {
        this.filtered_kecamatan_data = this.kabupaten_data;
      }
    },

    async onSubmit() {
      // console.log(this.checkbox);
      if (
        this.checkbox &&
        this.input.name != null &&
        this.input.email != null &&
        this.input.phone != null &&
        this.ValidateEmail(this.input.email) &&
        this.ValidatePhone(this.input.phone)
      ) {
        this.dialog = true;
        var username =
          this.input.name[0].toUpperCase() + this.input.name.slice(1);

        let selected_provinsi = this.provinsi_data.find(
          (o) => o.tbmprov_provinsi_id == this.input.provinsi
        );
        let selected_kabupaten = this.kabupaten_data.find(
          (o) => o.tbmkt_kota_id == this.input.kabupaten
        );
        let selected_kecamatan = this.kecamatan_data.find(
          (o) => o.tbmkct_kecamatan_id == this.input.kecamatan
        );

        // console.log(
        //   this.input.alamat +
        //     ", " +
        //     selected_kecamatan.tbmkct_nama +
        //     ", " +
        //     selected_kabupaten.tbmkt_nama +
        //     ", " +
        //     selected_provinsi.tbmprov_nama
        // );
        // console.log(username, this.input.email, this.input.phone);
        // console.log(this.voucher_type);
        try {
          var dateNow = new Date().toISOString().slice(0, 10).replace(/-/g, "");
          var tgl = dateNow.toString();
          var kunci =
            "SSO" +
            "4lfag1ft" +
            tgl +
            "precise" +
            this.input.email.toLowerCase();
          // console.log(kunci);
          var token = sha256(kunci);

          // console.log(this.$route.params.router_data);

          var body = {
            type: this.voucher_type,
            pe_id: this.pe_id,
            name: username,
            email: this.input.email.toLowerCase(),
            phone: this.input.phone,
            body_html: this.$route.params.router_data,
            subject_html: this.subject_mail,
            client_ip: this.client_ip,
            alamat: this.input.alamat,
            kecamatan: selected_kecamatan.tbmkct_nama,
            kabupaten: selected_kabupaten.tbmkt_nama,
            provinsi: selected_provinsi.tbmprov_nama,
          };

          const headers = {
            "Access-Control-Allow-Origin": "*",
            "Access-Control-Allow-Credentials": "true",
            "Access-Control-Allow-Methods":
              "GET, PUT, POST, DELETE, OPTIONS, post, get",
            "Access-Control-Allow-Headers":
              "Origin, Content-Type, X-Auth-Token",
            "Content-Type": "application/json",
            "Content-Length": "<calculated when request is sent>",
            Host: "<calculated when request is sent>",
            Accept: "*/*",
            "Accept-Encoding": "gzip, deflate, br",
            Connection: "keep-alive",
            app: "sso",
          };
          // console.log(body);
          await axios
            .post(api_url + "/assign-product?token=" + token, body, headers)
            .then((response) => {
              console.log(response.data);
              if (response.data.response_message == "success") {
                this.$router.push({ name: "product" });
                localStorage.removeItem("voucher_token");
                localStorage.removeItem("pe_id");
                localStorage.removeItem("voucher_type");
                localStorage.removeItem("pe_end_date");
                localStorage.removeItem("subject_mail");
                localStorage.removeItem("client_ip");
                localStorage.removeItem("product_name");
                localStorage.removeItem("pe_whitelist_kabupaten");
              } else if (
                response.data.response_message.includes(
                  "Error : duplicate key value violates unique constraint"
                )
              ) {
                this.$router.push({ name: "email-pernah-digunakan" });
                localStorage.removeItem("voucher_token");
                localStorage.removeItem("pe_id");
                localStorage.removeItem("voucher_type");
                localStorage.removeItem("pe_end_date");
                localStorage.removeItem("subject_mail");
                localStorage.removeItem("client_ip");
                localStorage.removeItem("product_name");
                localStorage.removeItem("pe_whitelist_kabupaten");
              } else {
                alert(
                  "Gagal mengirim email komfirmasi. Silahkan coba beberapa saat lagi."
                );
              }
            })
            .catch((error) => {
              console.log("Form.vue (error) :: ", error);
            });
        } catch (e) {
          console.error(e);
          alert("Error: koneksi gagal");
        }
      } else {
        alert(
          "Silahkan melengkapi isi form dan menyetujui Terms and Conditions"
        );
      }

      this.dialog = false;
    },
    ValidateEmail(mail) {
      if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(mail)) {
        return true;
      }
      alert("You have entered an invalid email address!");
      return false;
    },
    ValidatePhone(phone) {
      if (/^\d+$/.test(phone)) {
        return true;
      }
      alert("Nomor Hp tidak valid!");
      return false;
    },
  },
};
</script>

